import React from "react";
import {Modal, Button, FormLabel, FormControl, FormGroup, Form} from "react-bootstrap";
import {confirmable, createConfirmation} from "react-confirm";

class AddSongToTabActivedDialog extends React.Component {
    constructor(props) {
        super(props);
        this.titleRef = React.createRef();
        this.vidRef = React.createRef();
    }

    handleOnClick() {
        const {proceed} = this.props;
        return () => {
            let vidGet = this.vidRef.current.value.match(/(?<=^https:\/\/youtu.be\/|^https:\/\/www.youtube.com\/watch\?(.*?)(^|\/|v=))[a-z0-9_-]{11}/gim);
            proceed({
                title: this.titleRef.current.value,
                vid: vidGet.length > 0 ? vidGet[0] : ""
            });
        };
    }

    render() {
        const {show, dismiss} = this.props;

        return (
            <div className="static-modal">
                <Modal show={show} onHide={dismiss}>
                    <Modal.Body>
                        <FormGroup as={Form.Row}>
                            <FormLabel>Title: </FormLabel>
                            <FormControl type="text" ref={this.titleRef} defaultValue={this.props.title}/>
                            <FormLabel>VideoID: </FormLabel>
                            <FormControl type="text" ref={this.vidRef} defaultValue={this.props.vid} placeholder="Youtube Url or VideoID"/>
                            <Button className="button-l"
                                    bsStyle="default"
                                    onClick={this.handleOnClick()}>
                                Confirm
                            </Button>
                        </FormGroup>
                    </Modal.Body>
                </Modal>
            </div>
        );
    }
}

export const confirm = createConfirmation(confirmable(AddSongToTabActivedDialog));
