import React from "react";
import {Modal, Button, FormLabel, FormControl, FormGroup, Form} from "react-bootstrap";
import {confirmable, createConfirmation} from "react-confirm";

class AddTabDialog extends React.Component {
    constructor(props) {
        super(props);
        this.nameRef = React.createRef();
    }

    handleOnClick() {
        const {proceed} = this.props;
        return () => {
            proceed({
                output: this.nameRef.current.value
            });
        };
    }

    render() {
        const {show, dismiss, name} = this.props;

        return (
            <div className="static-modal">
                <Modal show={show} onHide={dismiss}>
                    <Modal.Body>
                        <FormGroup as={Form.Row}>
                            <FormLabel>Title: </FormLabel>
                            <FormControl type="text" ref={this.nameRef} defaultValue={name}/>
                            <Button className="button-l"
                                    onClick={this.handleOnClick()}>
                                Confirm
                            </Button>
                        </FormGroup>
                    </Modal.Body>
                </Modal>
            </div>
        );
    }
}

export const confirm = createConfirmation(confirmable(AddTabDialog));
